.App {
  text-align: center;
}

.navbar {
  width: 100%;
  position: relative;
  top: 0;
  height: 50px;
  background-color: black;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.company-icon {
  width: 20%;
  display: flex;
  align-items: center;
}
.company-icon img {
  width: 100%;
  max-width: 200px;
}
.size-input {
  display: flex;
  align-items: center;
  width: 50%;
  justify-content: center;
}

.size-input input {
  height: 24px;
  border-radius: 2px;
  text-align: center;
  margin: 0 10px;
  border: 1px solid #ff367a;
  background: lightgray;
}
.button {
  border-radius: 2px;
  background-color: #ff367a;
  height: 30px;
  border: none;
  cursor: pointer;
  min-width: 105px;
  margin: 0 10px;
}
.submission-buttons {
  display: flex;
  width: 30%;
  justify-content: flex-end;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 10;
}

.modal-main {
  position: fixed;
  width: 80%;
  height: 500px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #ff367a;
  border-radius: 10px;
  padding: 10px 0;
  overflow: hidden;
  color: #ff367a;
}
.modal-background {
  position: absolute;
  left: 0;
  top: -145px;
  width: 100%;
  height: auto;
  opacity: 1;
  z-index: -1;
}
.modal-background-dimmer {
  background-color: gray;
  position: absolute;
  left: 0;
  top: -10px;
  width: 100%;
  height: auto;
  opacity: 0.3;
  z-index: -1;
  height: 110%;
  width: 100%;
}
.modal-form {
  width: 60%;
  padding: 10px 0;
  margin: auto;
}
.form-row {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.form-row input {
  width: 300px;
  height: 24px;
  border-radius: 2px;
  text-align: center;
  margin: 0 10px;
  border: 1px solid #ff367a;
  background: white;
  color: black;
}
.form-row textarea {
  width: 100%;
  height: 200px;
  border-radius: 2px;
  text-align: center;
  margin: 0 10px;
  border: 1px solid #ff367a;
  background: white;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}
.container {
  display: flex;
  height: 94.6vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: relative;
  overflow: hidden;
  color: white;
}
.container-background {
  width: auto;
  position: absolute;
  z-index: -5;
  opacity: 0.2;
  max-width: 100%;
}
.object-list-container {
  width: 20%;
  min-width: 400px;
  border-right: 1px solid black;
  overflow-y: scroll;
}

.object-list-container::-webkit-scrollbar {
  width: 5px;
}
.object-list-container::-webkit-scrollbar-thumb {
  background: #ff367a;
  height: 10px;
  border-radius: 15px;
}
.object-item-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 50px;
}
.object-item {
  max-width: 40%;
}
.object-item-image {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.object-item-image img {
  max-height: 100%;
  max-width: 80%;
  cursor: grab;
}
.delete-component {
  position: absolute;
  width: 50px;
  height: 50px;
  padding: 5px;
}
.delete-component img {
  width: 75%;
  cursor: pointer;
}
.rotate-component {
  position: absolute;
  width: 50px;
  height: 50px;
  padding: 5px;
  left: 60px;
}

.rotate-component img {
  width: 80%;
  cursor: pointer;
}
.grid-container {
  position: relative;
  width: 65%;
  height: 100%;
  overflow: auto;
}

.grid-container::-webkit-scrollbar {
  height: 5px;
  width: 5px;
}

.grid-container::-webkit-scrollbar-thumb {
  background: #ff367a;
  border-radius: 15px;
}

.width-distance {
  position: absolute;
  top: -30px;
  display: inline;
  left: 50%;
}
.height-distance {
  position: absolute;
  top: 50%;
  left: -40px;
  transform: rotate(-90deg);
}
.grid-box {
  width: 500px;
  position: relative;
  height: 500px;
  margin: 50px auto 50px;
  background-color: grey;
}
.grid {
  width: 500px;
  position: relative;
  height: 500px;
  background-color: grey;
  background-size: 28px 28px;
  background-image: linear-gradient(to right, dimgrey 2px, transparent 1px),
    linear-gradient(to bottom, dimgrey 1px, transparent 1px);
  border: 1px solid black;
}
.grid:active {
  cursor: grabbing;
}
.grid-item {
  position: absolute;
}
.grid-item img {
  max-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}
.active {
  animation-name: pulse;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

.layout {
  background-color: grey;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 3px solid black;
}
.react-grid-item {
  cursor: pointer;
}

.counter-container {
  width: 15%;
  border-left: 1px solid black;
  padding: 0 10px;
}
.counter-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.counter-row span {
  text-align: left;
}
.rotated {
  transform: rotate(90deg) !important;
}

@keyframes pulse {
  from {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
  to {
    opacity: 1;
  }
}
